import React, { Component } from "react";
import "./help.css"
import draw from "../assets/draw.gif"
import selectAlgo from "../assets/select-algo.gif"

import DemoNode from "./demo-node";
import NodeStates from "./node-states";



export class Help extends Component {

    constructor(props) {
        super(props);

        this.state = {
            isVisible: true,
        }
    }

    componentDidUpdate(prevProps) {
        if (prevProps.isVisible !== this.props.isVisible) {
            this.setState({ isVisible: this.props.isVisible })
        }
    }

    handleBackgroundClick = (e) => {
        e.stopPropagation();
        this.props.toggleHelpPanel()
    }

    handlePanelClick = (e) => {
        e.stopPropagation();
    }

    render() {
        if (this.state.isVisible) {
            return (
                <div className="Background" onClick={this.handleBackgroundClick}>
                    <div className="HelpContainer" onClick={this.handlePanelClick}>
                        <h2 className="Title">Welcome To My Pathfinding Visualizer</h2>

                        <div className="NodeDisplay">
                            <div className="NodeContainer">
                                <DemoNode nodeState={NodeStates.UNVISITED} isStart={true} />
                                    Start Node
                                </div>

                            <div className="NodeContainer">
                                <DemoNode nodeState={NodeStates.UNVISITED} isEnd={true} />
                                    End Node
                                </div>

                            <div className="NodeContainer">
                                <DemoNode nodeState={NodeStates.WALL} isStart={false} />
                                    Wall Node
                                </div>

                            <div className="NodeContainer">
                                <DemoNode nodeState={NodeStates.VISITED} isStart={false} />
                                    Visited Node
                                </div>

                            <div className="NodeContainer">
                                <DemoNode nodeState={NodeStates.SHORTESTPATH} />
                                    Path Node
                                </div>
                        </div>

                        <div className="Card">
                            <div className="NodeContainer">
                                <img src={selectAlgo} width="530px" height="75px" alt="" />
                                <span className="Text">Select a path finding or maze algorithm!</span>
                            </div>

                            <div className="NodeContainer">
                                <img src={draw} width="200px" height="200px" alt="" />
                                <span className="Text">Draw walls by clicking and dragging!</span>
                            </div>
                        </div>

                        <button className="CloseButton" onClick={this.handleBackgroundClick}>Got It!</button>
                    </div>
                </div >

            );
        } else {
            return (
                <div></div>
            )
        }
    }
}