import React, { Component } from "react";
import "./demo-node.css";
import NodeStates from "./node-states";

export default class DemoNode extends Component {
    constructor(props) {
        super(props);
        this.state = {
            nodeState: NodeStates.UNVISITED,
            isStart: false,
            isEnd: false
        };
    }

    /** Change State based on property nodeState */
    static getDerivedStateFromProps(nextProps) {
        return {
            nodeState: nextProps.nodeState,
            isStart: nextProps.isStart,
            isEnd: nextProps.isEnd
        };
    }

    render() {

        if (this.props.isStart) {
            return (
                <div
                    className="DemoNode"
                >
                    <div
                        className="DemoInsideStart"></div>
                </div>
            );
        } else if (this.props.isEnd) {
            return (
                <div
                    className="DemoNode"
                >
                    <div
                        className="DemoInsideEnd"></div>
                </div >
            );
        } else if (this.state.nodeState === NodeStates.WALL) {
            return (
                <div className="DemoWallNode"></div>
            );
        } else if (this.state.nodeState === NodeStates.UNVISITED) {
            return (
                <div
                    className="DemoNode"
                >
                </div>
            );
        } else if (this.state.nodeState === NodeStates.VISITED) {
            return (
                <div
                    className="DemoVisitedNode"
                >
                    <div></div>
                </div>
            );
        } else if (this.state.nodeState === NodeStates.SHORTESTPATH) {
            return (
                <div
                    className="DemoShortestPathNode"
                >
                    <div></div>
                </div>
            );
        } else {
            return <div>no such state</div>;
        }
    }
}
